export default {"name":"云南红茶","img":"\\hongcha\\yunnanhuongcha\\img_20191122110235gzfe1a.jpg","desc":["云南红茶简称滇红，属红茶类。由汉族茶农创制于民国年间。产于云南省南部与西南部的临沧、保山、凤庆、西双版纳、德宏等地。以大叶种红碎茶拼配形成，定型产品有叶茶、碎茶、片茶、末茶4类11个花色。其外形各有特定规格，身骨重实，色泽调匀，冲泡后汤色红鲜明亮，金圈突出，香气鲜爽，滋味浓强，富有刺激性，叶底红匀鲜亮。制作采用优良的云南大叶种茶树鲜叶，先经萎凋、揉捻或揉切、发酵、烘烤等工序制成成品茶。"],"menus":[{"name":"云南红茶茶叶历史","content":[{"text":"1937年秋，冯绍裘和郑鹤春两位到云南实地观察并调查茶叶产销情况，觉得凤庆县的凤山有着很适合茶叶的生长自然条件，于是开始试制\n        红茶\n        ;通过努力，试制成功。茶叶条索肥实，汤色红浓明亮，叶底红艳发光，香味浓郁，为国内其他省小叶茶种所未见。在试制红茶的期间，由于没有现在这样通畅的公路，机器设备必须在大理拆卸成零，用马帮驮运到凤庆，来回需费时半月。马帮在金舵从大理至凤庆之间的路程中有一条五尺宽的石板山路必须在江边放下驮子，商人们乘竹筏而过，马自己凫水到对岸。为了试制成功，冯绍裘等人土法上马，用木质揉桶，脚踏烘干机，竹编烘笼等等，保证了试制工作的顺利展开。"},{"text":"1939年，第一批滇红500担终于试制成功了，先用竹编茶笼装运到香港，再改用木箱铝罐包装投入市场。滇红茶创制出来了，冯老先生终从众人之意，定名“滇红”;“滇红”名茶就这样诞生了。此后，滇红茶产业年年向前发展，成为祖国茶叶生产上一朵璀璨的名茶之花;滇红特级工夫茶，中国著名红茶;其在前苏联、东欧各国和伦敦市场上享有崇高声誉，是中国出口红茶中售价高创汇多的佼佼者。"}]},{"name":"云南红茶茶叶特点","content":[{"text":"云南\n        红茶\n        的外形各有特定规格，身骨重实，色泽调匀，冲泡后汤色红鲜明亮，金圈突出，香气鲜爽，滋味浓强，富有刺激性，内质香气甜醇，汤色红艳，滋味鲜爽浓强，叶底红匀明亮。"},{"src":"\\hongcha\\yunnanhuongcha\\img_20191122110235izirqn.jpg"}]},{"name":"云南红茶加工工艺","content":[{"text":"制作"},{"text":"滇红制作系采用优良的云南大叶种茶树鲜叶，先经萎凋、揉捻或揉切、发酵、烘烤等工序制成成品茶;再加工制成滇红功夫茶，又经揉切制成滇红碎茶。上述各道工序，长期以来，均以手工操作。此工艺从1939年在凤庆与勐海县试制成功。"},{"text":"滇红工夫茶采摘1芽2、3叶的芽叶作为原料，经萎凋、揉捻、发酵、干燥而制成;滇红碎茶是经萎凋、揉切、发酵、干燥而制成。"}]},{"name":"云南红茶储藏方法","content":[{"text":"云南\n        红茶\n        的存储常见的方法有罐储存法和袋储存法："},{"text":"罐储存法：选用市面上的马口铁罐或铁听，也可采用原放置其他食品的铁听、铁筒，但是要消除原来的异味。家庭贮茶最流行和最常用的方法是用铁听贮茶，简单方便。装有茶叶的铁听，应置于阴凉处，不能放在阳光直射或潮湿的地方。"},{"text":"袋储存法：塑料袋保存茶叶是目前家庭贮茶最简便、最经济实用的方法之一。塑料袋是比较普遍的包装材料，品种多，价格低廉，使用随意。选用塑料袋时，首先选用适合食品用的包装袋;其次，材料本身不应有异味。"}]},{"name":"云南红茶冲泡方法","content":[{"text":"泡滇红的3个要点："},{"src":"\\hongcha\\yunnanhuongcha\\img_20191122110235isckx9.jpg"},{"text":"一、用量(4~5克)"},{"text":"二、泡10泡以上 [水温80-83度]，注意水温是要把水先烧开，再放置几分钟，稍微降温就行了。不要加冷水或者水不开就拿来用。"},{"text":"三、出汤要快 1~3泡都是1秒出汤，第一泡是洗茶，第4泡开始延长到3秒，5秒，8秒这样递增出汤之后不要盖盖。"}]},{"name":"云南红茶功效作用","content":[{"text":"1、利尿功效：在\n        红茶\n        中的咖啡碱和芳香物质联合作用下，可增加肾脏的血流量，提高肾小球过滤率，扩张肾微血管，并抑制肾小管对水的再吸收，于是促成尿量增加。如此有利于排除体内的乳酸、尿酸(与痛风有关)、过多的盐分(与高血压有关)、有害物等，以及缓和心脏病或肾炎造成的水肿。"},{"src":"\\hongcha\\yunnanhuongcha\\img_2019112211023594neq3.jpg"},{"text":"2、消炎杀菌功效：红茶中的多酚类化合物具有消炎的效果，再经由实验发现滇红茶功效与作用，儿茶素类能与单细胞的细菌结合，使蛋白质凝固沉淀，藉此抑制和消灭病原菌。所以细菌性喇疾及食物中毒患者喝红茶颇有益，民间也常用浓茶涂伤口、褥疮和香港脚。"},{"text":"3、解毒功效：据实验证明，红茶中的茶多碱能吸附重金属和生物碱，并沉淀分解，这对饮水和食品受到工业污染的现代人而言，不啻是一项福音。"},{"text":"4、提神消疲功效：经由医学实验发现，红茶中的咖啡碱藉由刺激大脑皮质来兴奋神经中枢，促成提神、思考力集中，进而使思维反应更形敏锐，记忆力增强;它也对血管系统和心脏具兴奋作用，强化心搏，从而加快血液循环以利新陈代谢，同时又促进发汗和利尿，由此双管齐下加速排泄乳酸(使肌肉感觉疲劳的物质)及其他体内老废物质，达到消除疲劳的效果。"},{"text":"5、生津清热功效：夏天饮红茶能止渴消暑，是因为茶中的多酚类、醣类、氨基酸、果胶等与口涎产生化学反应，且刺激唾液分泌，导致口腔觉得滋润，并且产生清凉感;同时咖啡碱控制下视丘的体温中枢，调节体温，它也刺激肾脏以促进热量和污物的排泄，维持体内的生理平衡。"}]},{"name":"云南红茶获奖情况","content":[{"text":"1986年1月，在北京召开的1985年国家优质食品授奖大会上，凤庆茶厂所产的云南\n        红茶\n        一级工夫茶和勐海茶厂所产的“红碎1号”分获国家优质食品银质奖。"},{"text":"1986年商业部在广州召开的全国名茶评比会上，凤庆茶厂的云南红茶工夫茶被评为全国名茶。"},{"text":"1989年农业部在西安召开的全国名茶、优质茶评选会上，昌宁茶厂生产的云南红茶一级工夫茶被评为全国名茶，勐海茶厂生产的“滇红碎茶1号”获优质茶称号。"},{"text":"1990年9月商业部在河南信阳市召开的全国名茶评比会上，凤庆县茶场生产的中茶牌云南红茶工夫茶再次荣获全国名茶称号。"}]},{"name":"云南红茶鉴别好茶","content":[{"text":"冲泡后的滇\n        红茶\n        汤红艳明亮，高档滇红，茶汤与茶杯接触处常显金圈，冷却后立即出现乳凝状的冷后浑现象，冷后浑早出现者是质优的表现。滇红工夫茶中，品质最优的是\"滇红特级礼茶\"，以1芽1叶为主制造而成，成品茶芽叶肥壮，苗锋秀丽完整，金毫显露，色泽乌黑油润，汤色红浓透明，滋味浓厚鲜爽，香气高醇持久，叶底红匀明亮。"},{"text":"优质红碎茶外形颗粒重实、匀齐、纯净，色泽油润，内质香气甜醇，汤色红艳，滋味鲜爽浓强，叶底红匀明亮。"}]}]}